

import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"

const NavItem = styled(Link)`
  text-decoration: none;
  color: #003764;
  display: inline-block;
  white-space: nowrap;
  margin: 0 3vw;
  transition: all 350ms ease-in;
  position: relative;
  width: 65%;
  text-align: center;

  :after {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 0%;
    content: ".";
    color: transparent;
    background: #0077c8;
    height: 3px;
    border-radius: 30px;
    transition: all 350ms linear;
  }

  :hover {
    color: #0077c8;
    font-weight: 700;
    ::after {
      width: 100%;
    }
  }

  @media (max-width: 768px) {
    padding: 20px 0;
    font-size: 1.5rem;
    z-index: 6;
  }
`
const NavbarLinks = () => {
  return (
    <>
      <NavItem to="/about">소개</NavItem>
      <NavItem to="/">포스트</NavItem>
      <NavItem to="/recruits">채용</NavItem>
    </>
  )
}

export default NavbarLinks